@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&family=Roboto:ital,wght@0,100..900;1,100..900&family=Space+Grotesk:wght@300..700&display=swap');

/* reset */
a{text-decoration: none;color: inherit;}
/* font */
body{font: 12px "Noto Sans KR", sans-serif; 
  // overflow-x: hidden;
}
/* common */
.inner{max-width: 1200px;margin: 0 auto;}
/* 변수 */
$main-color:#ffa143;
$sub-color:#d9d9d9;
$back-color:#e5e5e5;
$black-color:#000a19;
$white-color:#f0f9ff;
$roboto-font:"Roboto", sans-serif;
$Space-font:"Space Grotesk", sans-serif;
$font-size-10px:0.6rem;
$font-size-14px:0.8rem;
$font-size-16px:1rem;
$font-size-20px:1.25rem;
$font-size-32px:2rem;
$font-size-40px:2.5rem;
$font-size-50px:3.12rem;
$font-size-64px:4rem;
$font-size-80px:5rem;
/* font-Responsive */

html{font-size: 12px;}

/* 중간 화면 (예: 태블릿) */
@media (min-width: 900px) {
  html {
    font-size: 14px;
  }
}

/* 큰 화면 (예: 데스크탑) */
@media (min-width: 1200px) {
  html {
    font-size: 16px;
  }
}
/* 변수 */
@mixin title-h2{
  width: 100%;
  position: absolute;
  font-size: $font-size-80px;
  font-weight: bold;
  color: $main-color;
  top: 50%;
  text-align: center;
  transform: translateY( -50%);
  text-decoration: underline;
  text-underline-offset: 20px;
  z-index: 30;
}
/* style */
#page1{
  position: relative;
  z-index: 10;
  padding-bottom: 168px;
  height: 100vh;
  background-color: $white-color;
  .page1__inner{
    .page1__bg{
      height: 700px;
      position: absolute;
      left:48%;
      top: 300px;
      div{
        position: absolute;
        width:100px;height: 100px;
        border-radius: 50%;
        opacity: 0.2;
        animation: page1Bg1 4s infinite linear;
        background-color: $main-color;
      }
      .bg2{animation-delay: 0.5s;}
      .bg3{animation-delay: 1s;}
      .bg4{animation-delay: 1.5s;}
    }
    .page1__text{
      .text-3D{
        position: absolute;
        top: -100px;left:50%;
        transform: translateX(-50%);
        width: 100%;
        overflow: hidden;
        height: 800px;
        z-index: 10;
        .title-3d{
          height: 900px;
        }
      }
      .name{
        position:absolute ;
        top: 468px;left: 50%;
        transform: translateX(-50%);
        font: {
          size:$font-size-64px; family: $roboto-font; weight: bold;
        };
        color: $main-color;
        text-shadow: -2px 2px 2.8px rgba(0,0,0,0.5);
      }
      dl{
        display: inline-block;
        padding-left: 20px;
        margin-top:131px;
        font-weight: bold;
        opacity: 0.5;
        dt{
          padding-bottom: 2px;
          margin-bottom: 4px;
          border-bottom: 2px solid rgba(0,0,0,.5);
          font-size:$font-size-16px;
        }
        dd{
          padding-bottom: 5px;
          font-family:$roboto-font;
          font-size: $font-size-14px;
        }
      }
      .text_deco{
        display: block;
        margin-top: 330px;
        padding-right: 20px;
        text-align: right;
        p{
          display: inline-block;
          font: {size: $font-size-14px; weight: bold; align: left;};
          text-align-last: left;
          opacity: 0.5;
          span{font-size:$font-size-16px;}
        }
      }
    }
  }
  .page1__menu{
    position: relative;
    top: 70px;
    z-index: 30;
    display: flex;
    justify-content: center;
    ul{ 
      display: flex;
      width:50%;
      height: 100px;
      justify-content:space-evenly ;
      align-items: center;
      box-sizing: border-box;
      li{
        position: relative;
        outline: 0px solid $sub-color;
        border-radius: 50%;
        background-image:linear-gradient((rgba(0,0,0,0.5)),(rgba(0,0,0,0.5))) ,url(https://picsum.photos/id/100/100);
        a{
          display: block;
          width: $font-size-80px;height:$font-size-80px;
          border-radius:50% ;
          font: { family: $roboto-font; weight: 400; size: $font-size-14px;};
          text-align: center;
          line-height: 82px;
          color: $white-color;
          transition: 0.5s;
        }
        &::before{
          content: "";
          position: absolute;
          display: block;
          width: 100%;height: 100%;
          background-color: $back-color;
          top: 50%;
          left: 50%;
          border-radius: 50%;
          z-index: -1;
          transform: translate(-50%,-50%);
          transition: .5s;
        }
        &:hover{
          background-image:linear-gradient((rgba(0,0,0,0.3)),(rgba(0,0,0,0.3))) ,url(https://picsum.photos/id/100/100);
          position: relative;
          &::before{
            content: "";
            display: block;
            width:130%;height:130%;
          }
          a{
            width: 110px;height: 110px;
            font-weight: bold;
            line-height: 110px;
            transform: scale(1.2);
            font-size: $font-size-14px;
          }
        }
      }
    }
  }
  // page1__bg animatoin
  @keyframes page1Bg1 {
    0%{opacity: 0.2;transform: scale(0);}
    100%{opacity: 0;transform: scale(10);}
  }
}

//page2
#page2{
  @mixin rebon{
    position: absolute;
    transform: translateX(-50%);
    height: 100%;
    font-family: $roboto-font;
    font-weight: bold;
    min-width: 1920px;
  }
  overflow: hidden;
  position: relative;
  top:-200px;
  margin-bottom: -200px;
  height: 200vh;
  left: 0;
  color:$white-color;
  background-color: #000;
  background-image:linear-gradient(rgba(0,0,0,0.93) 50%,rgba(5, 5, 5, 0.95)100%), url('https://i.ibb.co/x3TCktM/645cbb8ec449398255b76326-noise.gif');
  .page2__inner{
    .page2__bg{
      position: absolute;
      top: -400px;
      height: 100%;
      left: 50%;
      .rebon{
        @include rebon();
        .rebon_text{
          color: #000;
          white-space: nowrap;
          box-sizing: border-box;
          .text1{
            top: 310px;
            left: -10px;
            width: 950px;
            height: 95px;
          }
          .shodow{
            position: absolute;top: 235px;left: 893px;
            width:45px;
            height: 120px;
            background-color: #B4B4B4;
            transform: skewY(-20deg) rotate(-10deg);
          }
          .text2{
            top: 160px;
            right: -20px;
            width: 55%;
            height: 120px;
            background-image: linear-gradient(90deg,rgba(0,0,0,0.2) 0%,rgba(0,0,0,0) 5%);
            span{line-height: 120px;}
          }
          &>div{
            position: absolute;
            overflow: hidden;
            transform: rotate(-10deg);
            background-color: $white-color;
          }
          span{
            line-height: 95px;
            display: inline-block;
            text-transform: uppercase;
            font-size: 53px;
          }
        }
        &.rebon1{
          opacity:0;
          top: -200px;
          left:100vw;
          span{
            animation: text-left 20s linear infinite;
          }
        }
        &.rebon2{
          opacity: 0;
          bottom: -1000px;
          left: -100vw;
          span{
            animation: text-right 20s linear infinite;
          }
        }
      }
    }
  }
  /* page2 bg right */
  @keyframes text-right{
    0%{transform: translateX(-50%);}
    100%{transform: translateX(0%);}
  }
  /* page2 bg left */
  @keyframes text-left{
    0%{transform: translateX(0%);}
    100%{transform: translateX(-50%);}
  }
  
  .page2__text{
    margin-top: 60px;
    font-size:$font-size-80px;
    letter-spacing: -3px;
    display: flex;
    justify-content: center;
    gap: 3rem;
    transform: scale(0.5);
    opacity: 0;
    color: $white-color;
    h2{
      position: relative;
      font-weight: initial;
      span{
        font-weight: bold;
        color: $main-color;
      }
    }
    p{
      font-family: $Space-font;
      font-size:11.7rem;
      display: inline;
      padding: 10px;
      height:5rem ;
      &:nth-of-type(1){
        transform:  rotate(180deg);
      }
      &:nth-of-type(2){
        align-self: flex-end;
      }
    }
  }
}

/* page3 */
#page3{
  position: relative;
  margin-top: -100px;
  background-image: linear-gradient(rgba(0,0,0,1) 0%,rgba(0,0,0,0) 20%), url(../images/page3-bg.jpg);
  background-size:contain ;
  background-position: left top;
  background-size: 110%;
  width: 100%;
  height: 975px;
  &::before{
    content: "";
    display: block;
    position: absolute;
    right: 0;
    width:45%;height: 100%;
    background-image: linear-gradient(#000,#666);
  }
  &::after{
    content: "";
    display: block;
    position: absolute;
    right: 0;
    width:45%;height: 100%;
    background-color: #B4B4B4;
    clip-path: polygon(0 0, 100% 0, 0 100%);
    opacity: 0.1;
  }
  .page3__inner{
    display: flex;
    position: absolute;left: 0;right: 0;
    top: 202px;
    .page3__left__prof{
      padding-right: 100px;
      flex: 1 1 50%;
      color: $white-color;
      .page3-3D{
        position: absolute;
        top: 138px;left: 16.7rem;
        overflow: hidden;
        border-radius: 50%;
        width: 23.9rem;height: 23.9rem;
        transform: scale(0.9);
        .character-3D{
          width: 400px;height:450px;;
        }
      }
      .box{
        max-width: 628px;
        h3{
          max-width: 628px;
          font-size:$font-size-32px;
          font-weight: 800;
          text-align: right;
          padding-bottom: 22px;
          color: $main-color;
          border-bottom: 6px solid $white-color;
          margin-bottom: 138px;
        }
        .text{
          box-sizing: border-box;
          padding: 50px 43px;
          border: 1px solid $white-color;
          max-width: 438px;
          span{font-family: $roboto-font;opacity: 0.8;font-size:$font-size-16px}
          .name{
            display: block;
            width: 100%;
            font-size:$font-size-50px;
            font-weight: 800;
            letter-spacing: 5px;
            margin-bottom: 20px;
          }
          a{
            font-size: $font-size-16px;
            display: inline-block;
            font-weight: 500;
            margin-bottom: 14px;
          }
          button{
            color: $white-color;
            background-color: transparent;
            outline: none;
            border: none;
            display: block;
            padding: 0;
            cursor: pointer;
            p.arrow{
              height: 1px;
              justify-content: flex-end;
              background-color: $white-color;
              width:148px;
              text-align: right;
              display: flex;
              align-items: center;
              margin-bottom: 20px;
            }
          }
          p.ect{
            font-size:$font-size-16px;
            opacity: 0.8;font-weight: 200;line-height: 1.8;
            max-width: 250px;
            word-break: keep-all;
          }
        }
      }
    }
    .page3__right__prof{
      color: $white-color;
      padding-left: 112px;
      flex: 1 1 42%;
      letter-spacing: 0;
      h3{
        font-family: $roboto-font;
        font-size:$font-size-50px;font-weight: bold;
        padding-bottom: 73px;
      }
      .profile-bio{
        letter-spacing: 0.1rem;
        dl{
          padding-bottom: 57px;
          dt{
            padding-bottom: 20px;
            font-size: $font-size-32px;font-weight: 500;
            text-transform: uppercase;
            font-weight: bold;
            font-family: $roboto-font;
          }
          dd{
            font-weight: 300;
            font-size: $font-size-20px;
            opacity: 0.8;
          }
        }
      }
    }
  }
  .page3__side__skill{
    overflow: hidden;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 46%;
    height: 72%;
    z-index: 1;
    .skill_bg{
      height: 100%;
      position: relative;
      .button{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        display: flex;
        align-items: center;
        color: $main-color;
        background-color: $sub-color;
        width: 120px;
        height: 335px;
        border-radius: 50%;
        i{
          font-size:40px;
          position: absolute;
          left: 50px;
          animation: arrow 2s infinite linear;
          &:nth-child(2){animation-delay: 1s;}
          &:nth-child(3){animation-delay: 2s;}
        }
      }
      .box{
        background-color:$sub-color;
        height: 100%;
        border-radius: 60px 0 0 60px;
        margin-left: 60px;
      }
    }
    .skill_icon{
      padding: 20px;
      width: 75%;
      font-family: $roboto-font;
      font-weight: bold;
      position: absolute;
      top: 50%;left:$font-size-80px;
      transform: translateY(-50%);
      h3{
        color: #838383;font-size: $font-size-40px;text-transform: capitalize;
        text-align: center;
        margin-bottom: 40px;
      }
      ul{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(4, 130px);
        column-gap: 6px;
        li{
          margin: 0 auto;
          padding-bottom:30px;
          & .img{
            width:5.6rem;height: 5.6rem;
          }
          @for $i from 1 through 12{
            &:nth-child(#{$i}) .img{
              background: url(../images/skill#{$i}.png) no-repeat center/cover;
            }
          }
          p{
            text-align: center;
            font-size: $font-size-16px;
            padding-top: 9px;
          }
        }
      }
    }
  }
  /* page3 skill_bg animate */
  @keyframes arrow{
    0%{transform: translateX(0);opacity: 0;}
    100%{transform: translateX(-35px) scale(1.5);opacity: 1;}
  }
}

/* page4 */
#page4{
  position: relative;
  overflow: hidden;
  height: 100%;
  background-color: #000;
  &::after{
    content: "";
    position: absolute;
    display: block;
    top: 0;left: 0;
    width: 100%;height: 100%;
    background-color:black;
    opacity:0;
  }
  .page4__inner{
    background-color: #000;
    overflow: hidden;
    height: 700px;
    .page4__bg{
      .circle_bg{  
        position: absolute;
        transform: translateX(-50%);
        top: -20%;
        left: 50%;
        width: 62.5rem;height:62.5rem;
        background-color: $sub-color;
        border-radius: 50%;
        z-index: -1;
      }
        .black_bg{
        background-color: rgba(0, 0, 0, 0);
        position: absolute;
        top: 0;left: 0;right: 0;bottom: 0;
        z-index: 20;
      }
    }

    h2{
      @include title-h2()
    }
    .page4__img{
      position: relative;
      z-index: 10;
      width: 50%;
      height: 100%;
      top: 0;
      left: 55%;
      transform: translateX(-50%);
      outline: 1px dotted red;
      .wep_img{
        margin-top: -100px;
        transform: rotate(20deg);
        &>div{
          background: url(https://picsum.photos/300/1000) no-repeat center/cover;
          position: absolute;
          width:25%;height: 1150px;
          border-radius: 10px;
        }
        &.img1 div {
          top:-600px;
          left: 0;
        }
        &.img2 div {
          top:600px;
          left: 30%;
        }
        &.img3 div {
          top: -700px;
          left: 60%;
        }
        &.img4 div {
          top:500px;
          left: 90%;
        }
      }
    }
    .page4__text{
      p{
        width: 100%;
        position: absolute;
        font-size: $font-size-14px;
        opacity: 0.5;
        font-weight: bold;
        &:nth-child(1){top: 93px;left:110%;}
        &:nth-child(2){top: 163px; left: 110%;font-size: $font-size-16px;}
        &:nth-child(3){top: 209px;left: 110%;opacity: 0.2;}
        &:nth-child(4){right: 10%;bottom: 87px;}
        &:nth-child(5){right: 10%;bottom: 135px;opacity: 0.7;}
        &:nth-child(6){right: 10%;bottom: 197px;font-size: $font-size-16px;opacity: 0.4;}
      }
    }
  }
}

/* page5 */
#page5{
  position: relative;
  overflow: hidden;
  background-color: $back-color;
  h3{
    font-family: $roboto-font;
    font-size: $font-size-32px;
    text-transform: uppercase;
    font-weight: bold;
    margin-top: 93px;
    text-align: right;
    opacity: 0.5;
    padding-bottom: 25px;
  }
  .page5__slide{
    position: relative;
    overflow: visible;
    width: 96vw;
    margin-left: calc(-48vw + 50%);
    padding-bottom: 26px;
    &.swiper{
      .swiper-wrapper{
        width: 100%;
        display: flex;
        justify-content: space-between;
        .swiper-slide{
          margin-top: 40px;
          width: calc(100% / 3);
          height: 346px;
          transform-origin: bottom;
          border-radius: 50px 50px 0 0;
          background: url(https://picsum.photos/700/400) no-repeat;
          transition: 0.3s ease;
          opacity:0.6 ;
          box-shadow: 4px 11px 15px rgba(0,0,0,0.2);
          &.swiper-slide-active{
            transform: scale(1.1);
            z-index: 2;
            opacity: 1;
          }
        }
      }
      .slide_bar{
        position: absolute;
        left:-40px;
        width: 106%;
        height: 27px;
        background-color: #d3d3d3;
        clip-path: polygon(2vw 0, 98vw 0,100% 100%, 0 100%);
      }
      .swiper-button-prev,.swiper-button-next{color: #000;font-weight: bold;}
      .swiper-button-prev {left: 28%;}
      .swiper-button-next {right: 28%;}
    }
  }
  .Page5__trxt{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 99.1vw;
    height: 421px;
    background-color: $white-color;
    box-sizing: border-box;
    border-radius: 0 0 210px 210px;
    .page5__text__inner{
      height: 100%;
      display: flex;
      & article{
        max-width:1200px;
        height: 100%;
        position: absolute;
        transition: opacity 0.3s;

        h4{
          color: #ffa143;
          font-size: $font-size-40px;
          font-weight: bold;
          text-align: center;
          padding:  21px 0 21px;
        }
        p{
          width: 100%;
          font-size: $font-size-16px;
          line-height: 1.8;
          word-break: keep-all;
          text-align: justify;
          line-break: strict;
          text-justify: inter-word;
        }
        .color_key{
          display: flex;
          justify-content: flex-end;
          align-items: center;
          div{
            border: 3px solid rgba(0,0,0,0.3);
            width: $font-size-64px;
            height: $font-size-64px;
            border-radius: 50%;
            margin-left: $font-size-16px;
            &:nth-child(1){background-color:black ;}
            &:nth-child(2){background-color: red;}
            &:nth-child(3){background-color:orange ;}
            &:nth-child(4){background-color:palevioletred ;}
          }
        }
      }
    }
  }
}

/* page6 */
#page6{
  overflow: hidden;
  position: relative;
  height: 70vw;
  padding-top:200px;
  background-image: linear-gradient($back-color 0,$white-color 70%);
  .page6__inner{
    position: relative;
    width: 100%;
    height:25vw;
    .page6__bg{
      position: absolute;
      width: 100%;height: 100%;
      z-index: 10;
      background-color: rgb(255, 255, 255);
      opacity: 0.8;
    }
    .page6__img{
      height: 100%;
      padding-left: 20px;
      position: absolute;
      top: 50%;
      left:0vw;
      transform: translateY(-50%);
      ul{
        display: flex;
        width: 100%;
        height: 100%;
        column-gap:$font-size-20px ;
        box-sizing: border-box;
        li{
          flex: 0 0 auto;
          height: 100%;
          aspect-ratio: 1 / 1;
          background: url(https://picsum.photos/200)no-repeat center/cover;
        }
      }
    }
    .page6__text{
      width: 100%;height: 100%;
      h2{
        opacity: 1;
        @include title-h2();
      }
    }
  }
}

/* page7*/
#page7{
  position: relative;
  padding-bottom: 157px;
  z-index: 10;
  .page7__inner{
    overflow: hidden;
    position: relative;
    height: 1380px;
    h3{
      padding-top:64px;
      font-size: $font-size-32px;
      color: #ffa143;
      font-family: $roboto-font;
      font-weight: bold;
      text-transform: uppercase;
      text-align: right;
    }
    .page7__img{
      margin-top: 77px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 12px 16px;
      img{
        border-radius:7%;
        width: 100%;height: 100%;object-fit: cover;
      }

    }
    .page7__button{
      outline: 1px dotted red;
      position: absolute;
      top: 60px;
      left: 0;
      a{
        color: $white-color;
        display: flex;
        align-items: center;
        padding: 14px 25px;
        background-color: $main-color;
        border-radius: 30px;
        font-size: $font-size-20px;
        span{
          font-weight: bold;
          padding-right: 20px;
        }
        p{
          width: 80px;height: 3px;background-color: $white-color;
          text-align: right;line-height: 3px;
        }
      }
    }
  }
  .page7__click{
    position: absolute;
    bottom: -40px;
    left: 50%;
    transform: translateX(-50%);
    background-color: $white-color;
    padding: 18px 72px;
    border-radius:70px;
    text-transform: uppercase;
    p{
      cursor: pointer;
      display: inline;
      text-align: center;
      font-size: $font-size-40px;
      font-weight: bold;
      opacity: 0.7;
    }
  }
}

/* page8 */
#page8{
  position: relative;
  overflow: hidden;
  padding-bottom: 90px;
  background-color: #000;

  .page8__mouse{

    position: absolute;
    top: 0;left: 0;
    background-color: #000;
    .spliner{
      width: 100vw;
      height: 100vw;
      position: absolute;
    }
  }
  .page8__inner{

    position: relative;
    color: $white-color;
    .page8__text{
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      p{
        color: $main-color;
        font-size: $font-size-40px;
        font-weight: bold;text-align: center;
        padding-top: 378px;
      }
      & > span{
        padding-top: 46px;
        display: block;
        text-align: center;
        font-size: 8rem;
        font-family: $roboto-font;
        font-weight: bold;
        text-transform: uppercase;
      }
      .page8__connect{
        padding-right: 4px;
        display: flex;
        font-family: $roboto-font;
        font-size: 1rem;
        text-align-last: left;
        font-weight: normal;
        span{
          padding-right: 36px;
          letter-spacing: 2px;
        }
      }
    }
    .page8__menu{
      padding-bottom: 12px;
      padding-top: 332px;
      opacity: 0.6;
      text-transform: uppercase;
      ul{
        display: flex;
        li{
          display: inline-block;
          height: 8px;
          display: flex;
          align-items: center;
          height: 8px;
          &:not(:last-child){
            border-right: 1px solid $white-color;
            padding-right: 18px;
          }
          &:not(:first-child){
            padding-left: 18px;

          }
        }
      }
    }
    .top_button{
      position: absolute;
      bottom: 0;right: 0;
      p{
        text-align: center;
        line-height: 100px;
        cursor: pointer;
        border-radius: 50%;
        width: 100px;height: 100px;
        background-color: $main-color;
        text-transform: uppercase;
        font-weight: bold;
        color: #000;
        font-size: $font-size-32px;
      }
    }
  }
}
